.neon-input {
  width: 100%;
  padding: 12px;
  background: rgba(255,255,255,0.02);
  border-radius: 12px;
  border: 1px solid rgba(140, 90, 255, 0.4);
  backdrop-filter: blur(6px);
  color: #fff;
  box-shadow: 0 0 6px rgba(140, 90, 255, 0.35),
    inset 0 0 6px rgba(140, 90, 255, 0.15);
  transition: 0.2s;
}
.neon-input:focus {
  outline: none;
  border-color: rgba(180, 120, 255, 0.8);
  box-shadow: 0 0 12px rgba(140, 90, 255, 0.8),
    inset 0 0 10px rgba(140, 90, 255, 0.25);
}
.neon-box {
  background: rgba(255, 255, 255, 0.02);
  border-radius: 12px;
  backdrop-filter: blur(10px);
  transition: 0.2s;
}
.neon-btn {
  color: #fff !important;
  border: 1px solid rgb(165, 80, 255);
  border-radius: 8px;
  background: color-mix(in srgb, rgb(165,80,255) 20%, transparent);
  box-shadow: 0 0 4px rgb(165,80,255),
    inset 0 0 0.5px rgb(165,80,255);
  padding: 6px 20px;
  transition: 0.2s ease;
}
.neon-btn:hover {
  background: var(--btn-color);
  @supports (color: color-mix(in lab, red, red)) {
    background: color-mix(in srgb, var(--btn-color) 30%, transparent);
  }
  box-shadow: 0 0 3px var(--btn-color),
    inset 0 0 2px var(--btn-color),
    0 0 8px var(--btn-color);
  transform: translateY(-1px);
}
.gray-btn {
  color: #fff !important;
  border: 1px solid transparent;
  border-radius: 8px;
  padding: 10px 25px;
  background: transparent;
  transition: 0.2s ease;
}
.gray-btn:hover {
  background: rgba(255, 255, 255, 0.05);
}
.gray-btn.active,
.gray-btn[data-active="true"] {
  color: #fff !important;
  background: color-mix(in srgb, rgb(165, 80, 255) 20%, transparent);
  box-shadow: 0 0 4px rgb(165, 80, 255),
    inset 0 0 0.5px rgb(165, 80, 255);
}
.transparent-btn {
  background: transparent !important;
  box-shadow: none !important;
}
.transparent-btn:hover {
  box-shadow: none !important;
  transform: none !important;
}
.dropzone {
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  color: #aaa;
  cursor: pointer;
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid rgba(140, 90, 255, 0.5);
  border-radius: 12px;
  padding: 16px;
  backdrop-filter: blur(10px);
  transition: 0.2s;
  box-shadow: 0 0 8px rgba(140, 90, 255, 0.4),
    inset 0 0 8px rgba(140, 90, 255, 0.15);
}
.dropzone:hover {
  box-shadow: 0 0 12px rgba(140, 90, 255, 0.7),
    inset 0 0 12px rgba(140, 90, 255, 0.25);
  border-color: rgba(140, 90, 255, 0.8);
